.portfolio{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    height: 70vh;
    }
.Swiper{
    overflow: visible !important;
}
.portfolio-slider{
    margin-top: 3rem;
    width: 100%;
}
.portfolio-slider .SwiperSlide{
    width: 20rem;
}
.portfolio img{
    width: 19rem;
    filter: drop-shadow(-12px 15px 13px rgba(0, 0, 0, 0.25));
border-radius: 19px;
}


@media(max-width:767px){
    .portfolio img{
        transform: scale(0.8);
    }
}

@media(max-width:541px){
.portfolio img{
    transform: scale(0.8);
    width: 16rem;
}
}

@media(max-width:420px){
.portfolio img{
    width: 12rem;
    
}
}
